.photo {
  height: 100vh;
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: -37px;
    left: -37px;
    background-image: url(#{$baseUrl}asset/image/grosvenor-crescent-1-1700x1300.png);
    background-size: cover;
  }
}

